package app.cash.redwood.layout.compose

import app.cash.redwood.layout.api.CrossAxisAlignment
import app.cash.redwood.layout.modifier.Grow
import app.cash.redwood.layout.modifier.HorizontalAlignment
import app.cash.redwood.layout.modifier.Shrink
import app.cash.redwood.layout.modifier.VerticalAlignment
import kotlin.Any
import kotlin.Boolean
import kotlin.Double
import kotlin.Int
import kotlin.String
import app.cash.redwood.layout.api.Margin as ApiMargin
import app.cash.redwood.layout.modifier.Margin as ModifierMargin

internal class GrowImpl(
  public override val `value`: Double,
) : Grow {
  public override fun equals(other: Any?): Boolean = other is Grow
  && other.`value` == `value`

  public override fun hashCode(): Int {
    var hash = 17
    hash = 31 * hash + `value`.hashCode()
    return hash
  }

  public override fun toString(): String = """Grow(value=$value)"""
}

